import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import './Header.css';
import logo from '../../images/logo.svg';
import accountIcon from '../../images/account-icon.svg';
import menuIcon from '../../images/menu-icon.svg';

function Header(props) {
    const { pathname } = useLocation();

    return (
        <header className={`header ${pathname !== '/' ? '' : 'header_color_pink'}`}>
            <Link to='/'>
                <img className='logo' src={logo} alt=' ' />
            </Link>
            {props.loggedIn ?
                <nav className='header__navigation header__navigation_logged-in'>
                    <div className='header__links'>
                        <div className='header__films'>
                            <Link className='header__link header__link_type_films' to='/movies'></Link>
                            <Link className='header__link header__link_type_saved-films' to='/saved-movies'> </Link>
                        </div>
                        <div className='account-link'>
                            <Link className='header__link header__link_type_account' to='/profile'></Link>
                            <img className='account-link__icon' src={accountIcon} alt=' ' />
                        </div>
                    </div>
                    <img className='header__menu-button' src={menuIcon} alt=' ""' onClick={props.onMenuClick} />
                </nav>
                :
                <nav className='header__navigation'>
                    <Link className='header__signup-button' to='/signup'></Link>
                    <Link className='header__singnin-button' to='/signin'></Link>
                </nav>}
        </header>
    )
}

export default Header